import React from 'react'
import { useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { changeColor } from './slices/theme'
import { setGalleryEndpoint } from './slices/galleryConfigSlic'
import { setGalleryToken } from './slices/galleryTokenSlic'

import Switch from '@mui/material/Switch';
import FormLabel from '@mui/material/FormLabel';
import Grid from '@mui/material/Grid';
import MenuBookIcon from '@mui/icons-material/MenuBook';
import ManageAccountsIcon from '@mui/icons-material/ManageAccounts';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';

import Button from '@mui/material/Button';

import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import axios from 'axios'

import { useNavigate } from "react-router-dom";


export default function ThemeSwith() {
  //global state
  const theme = useSelector((state) => state.theme.value)
  const galleryEndpoint = useSelector((state) => state.galleryEndpoint.value)

  const refGalleryEndpoint = useRef();
  const refGalleryToken = useRef();

  const dispatch = useDispatch()

  //local state
  const [open, setOpen] = React.useState(false);
  const [authState, setAuthState] = React.useState({"state":false,"profile":null});
  const [message, setMessage] = useState(galleryEndpoint);
  const [token, setToken] = useState('');
  const [currentPage,setCurrentPage]=useState('/');


  const redirect = useNavigate();

  const handleClickOpen = () => {
    //setOpen(true);
    if (currentPage!="/"){
      redirect("/");
    }
  };

  const handleClose = () => {
    setOpen(false);
  };


  const handleChange = event => {
    setMessage(event.target.value);
  };

  const handleTokenChange = event => {
    setToken(event.target.value);
  };

  const handleSetGalleryEndpoint = (endpoint, token, dispatch) => {
    setOpen(false);
    if (endpoint.endsWith("/")) {
      endpoint = endpoint.substring(0, endpoint.length - 1)
    }

    if (token!=""){
      axios.post(endpoint + "/auth", { "token": token }).then((response) => {
        console.log(response.data);
        dispatch(setGalleryEndpoint(endpoint))
        dispatch(setGalleryToken(token))
      }).catch(function (error) {
        console.log(error.response)
        setToken("")
        setMessage("")
        dispatch(setGalleryToken(""))
      })
    }else{
      dispatch(setGalleryEndpoint(endpoint))
      dispatch(setGalleryToken(""))
      setToken("")
    }
    


  };

  return (
    <Grid>
      <Tooltip
        title={"Click setup AIGC Day gallery endpoint!"}
      >
        <IconButton aria-label="share" onClick={handleClickOpen}>
          <MenuBookIcon />
        </IconButton>
      </Tooltip>
      {' '}
      {/* <Tooltip
        title={"Click manage your endpoint!"}
      >
        <IconButton aria-label="share" onClick={() => {
          setAuthState(!authState)
          if (authState){
            setCurrentPage("/admin")
            redirect("/admin")
          }else{
            setCurrentPage("/login")
            redirect("/login")

          }
          
          }}>
          <ManageAccountsIcon />
        </IconButton>
      </Tooltip> */}
      
      <Switch
        onChange={() => dispatch(changeColor())}
        inputProps={{ 'aria-label': 'controlled' }}
      />
      <FormLabel id="demo-radio-buttons-group-label">{theme}</FormLabel>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>AIGC Day gallery</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Please enter a gallery address here, and you can share your prompt to galler.
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Gallery Address"
            type="text"
            fullWidth
            variant="standard"
            inputRef={refGalleryEndpoint}
            onChange={handleChange}
            value={message}
          />
          <TextField
            margin="dense"
            id="name"
            label="Admin Token"
            type="text"
            fullWidth
            variant="standard"
            inputRef={refGalleryToken}
            onChange={handleTokenChange}
            value={token}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
          <Button onClick={() => { handleSetGalleryEndpoint(`${refGalleryEndpoint.current.value}`, `${refGalleryToken.current.value}`, dispatch) }}>Submit</Button>
        </DialogActions>
      </Dialog>
    </Grid>
  )
}