import * as React from 'react';
import { useRef, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux'


//import mui 
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';
import Dialog from '@mui/material/Dialog';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Grid from '@mui/material/Unstable_Grid2'; // Grid version 1
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import ImageListItemBar from '@mui/material/ImageListItemBar';
import InfoIcon from '@mui/icons-material/Info';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';


import CircularProgress from '@mui/material/CircularProgress';
import Fade from '@mui/material/Fade';
import Switch from '@mui/material/Switch';
import FormLabel from '@mui/material/FormLabel';
import Tooltip from '@mui/material/Tooltip';
import 'react-medium-image-zoom/dist/styles.css'
import axios from 'axios'


function srcset(image, size, rows = 1, cols = 1) {
    return {
        src: `${image}`,
        srcSet: `${image}`,
    };
}

export default function GalleryUI() {

    const images = useSelector((state) => state.image.value)
    const galleryEndpoint= useSelector((state) => state.galleryEndpoint.value)
    const galleryToken= useSelector((state) => state.galleryToken.value)

    

    //load apiconfig
    const [apiLoadCount, setAPILoadCount] = useState(0);
    const [prompts, setPrompts] = useState([]);
    const [open, setOpen] = useState(false);
    const [selectPrompt, setSelectPrompt] = useState({});
    const [loading, setLoading] = React.useState(false);

    
    
    function handleClick() {
        setLoading((prevLoading) => !prevLoading);
    }



    const fetchImagesAsync = async () => {
        if (galleryEndpoint==""){
            return []
        }
        const response = await axios.get(galleryEndpoint+"/prompts")
        return response.data
    }

    function handleRemove(prompt_sha1) {
        if (galleryToken==""){
            console.log("need admin token")
            return
        }
        const config = {
            headers: {
                "X-Gallery-ADMIN-Token": galleryToken
            }
        };

        axios.delete(galleryEndpoint+"/prompts", { data:{ "prompt_sha1": prompt_sha1,"gallery_admin_token":galleryToken}}, config).then((response) => {
            console.log(response.data);
            const _prompts = fetchImagesAsync()
            _prompts.then((data) => {
                console.log(data)
                setPrompts(data)
            })
            
        }).catch(function (error) {
            console.log(error.response)
        })
    }


    if (prompts.length == 0 && apiLoadCount < 3) {
        const _prompts = fetchImagesAsync()
        _prompts.then((data) => {
            console.log(data)
            setPrompts(data)
        })
        const count = apiLoadCount + 1
        setAPILoadCount(count)

        console.log(_prompts)

    }



    const onChange = e => {
        console.log(e.target.value);
        setStyleOption(e.target.value);
    };



    const renderListOfImages = (images) => {

        return (

            <ImageList
                sx={{ width: 1024 + 256, height: 400 }}
                variant="quilted"
                cols={6}
                rowHeight={121}
            >
                {prompts.map((item) => (
                    <ImageListItem key={item.image_url} cols={1} rows={1}>
                        <img
                            {...srcset(item.image_url, 240, item.rows, item.cols)}
                            alt={item.seed}
                            loading="lazy"
                        />
                        {(galleryToken!="") && <Box>
                        <Tooltip 
                                title={"Click remove image from gallery!"}
                                key="{Math.random().toString(36)}"
                            >
                            <IconButton aria-label="remove" onClick={() => { 
                                console.log(item)    
                                handleRemove(item.prompt_sha1)
                            }
                            }>
                                <DeleteForeverIcon/>
                            </IconButton>
                        </Tooltip></Box>
                        }
                         {/* <IconButton sx={{ display: { xs: 'none', md: 'block' } }} key="{Math.random().toString(36)}" aria-label="share" onClick={() => {
                                    navigator.clipboard.writeText(item.image_url)
                                    console.log(item.image_url)
                                    setOpen(true)
                                    setSelectPrompt(item)

                                }}>
                                    <DeleteForeverIcon/>
                         </IconButton> */}
                        <ImageListItemBar
                            title={item.prompt.substring(0, 20)}
                            subtitle={<span>seed: {item.seed}</span>}
                            position="below"
                            actionIcon={
                                <IconButton key="{Math.random().toString(36)}" aria-label="share" onClick={() => {
                                    navigator.clipboard.writeText(item.image_url)
                                    console.log(item.image_url)
                                    setOpen(true)
                                    setSelectPrompt(item)

                                }}>
                                    <InfoIcon />
                                </IconButton>
                            }
                        />

                    </ImageListItem>
                ))}
            </ImageList>
        )


    }

    const handleShareClick = (imageName, prompt, seed) => {
        console.log(imageName, prompt, seed)
    }

    const handleClose = () => {
        setOpen(false)
    }


    React.useEffect(() => {
        const timer = setInterval(() => {
            if (loading){
                const _prompts = fetchImagesAsync()
                _prompts.then((data) => {
                    setPrompts(data)
                })
            }
       
        }, 5000);
        //must clearInterval 
        return () => clearInterval(timer);
        
    }, [loading]);



    return (
        <Box sx={{ flexGrow: 1 }}>
            <Grid container spacing={2}>
                <Grid xs={12}>
                    <Dialog
                        open={open}
                        onClose={handleClose}
                        hasCloseButton
                        style={{ maxWidth: "100%", maxHeight: "100%" }}
                    >
                        <DialogTitle>

                            {selectPrompt.model},  seed:  {selectPrompt.seed} , {selectPrompt.date}</DialogTitle>
                        <img
                            style={{ width: '100%', height: '100%' }}
                            src={selectPrompt.image_url}
                            alt="image"
                        />{' '}
                        <DialogContentText>
                            <Typography variant="button" display="block" gutterBottom>
                                Prompt:
                            </Typography> {selectPrompt.prompt}

                        </DialogContentText>

                    </Dialog>
                </Grid>
                <Grid xs={5}>
                </Grid>
                <Grid xs={4}>
                <Fade
                    in={loading}
                    style={{
                    transitionDelay: loading ? '800ms' : '0ms',
                    }}
                    unmountOnExit
                    >
                    <CircularProgress />
                </Fade>

                <Switch
                onChange={handleClick}
                inputProps={{ 'aria-label': 'controlled' }}
                />
                <FormLabel id="demo-radio-buttons-group-label">{'Auto Refresh'}</FormLabel>
                </Grid>
                <Grid xs={12}>
                    <Divider></Divider>
                </Grid>
                <Grid xs={12}>
                    {renderListOfImages(images)}
                </Grid>
            </Grid>
        </Box>

    );
}
